          <span></span>
          {{i.channel_info.name}}
        </p>
        <div class="cxy_content">
          <ul class="cxy_item">
            <!-- 跳到讲师详情 -->
            <li v-for="(item, index) in i.list" @click="sh(item.teacher_id)" :key="index">
              <div>
                <img :src="item.teacher_avatar" alt />
              </div>
              <div class="cxy_txt">
                <p>{{ item.teacher_name }}</p>
                <p>{{item.introduction}}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!-- 推荐课程 -->
