</template>

<script>
import appFooter from "../components/Footer";
import { gets, posts, getBanner } from "../util/api";
export default {
  components: { appFooter },
  data() {
    return {
      arr: [],
      list: [],
      show: false,
      banner: []
    };
  },
  created() {},
  mounted() {
    this.getList();
  },
  methods: {
    // 首页内容
    async getList() {
      // 内容数据
      let { data } = await gets("recommend/appIndex");
      console.log(data.data);
      this.arr = data.data;
      // banner数据
      let { data: res } = await getBanner();
      this.banner = res.data;
    },
    // 明星讲师跳讲师详情
    sh(id) {
      let zqd_token = localStorage.getItem("token");
      if (zqd_token) {
        this.$router.push(`/teacher?id=${id}`);
      } else {
        this.show = true;
      }
    },
    rili(){
      if(!localStorage.getItem("token")){
        this.show = true;
      }else{
        this.$router.push('/study-calendar')
      }
    }
  }
};
</script>

